import React, { useState, useEffect } from 'react'
import "./Chat.css"
import { useParams } from "react-router-dom"
import { InfoOutlined, StarBorderOutlined } from '@material-ui/icons';
import db from './firebase'
import Message from './Message'
import ChatInput from './ChatInput';
import instance from './axios';
import Pusher from 'pusher-js';

const Chat = () =>
{
    const { roomId } = useParams();
    const [roomDetails, setRoomDetails] = useState(null);
    const [roomMessages, setRoomMessages] = useState([]);

    const pusher = new Pusher('83a9269d53efd71b9741', {
        cluster: 'ap2'
    });

    const getConvo = () =>
    {
        instance.get(`/get/conversation?id=${roomId}`).then((res) =>
        {
            console.log(res.data);
            setRoomDetails(res.data.channelName);
            setRoomMessages(res.data.conversation);
        })
    }

    useEffect(() =>
    {
        // if (roomId) {
        //     db.collection("rooms")
        //         .doc(roomId)
        //         .onSnapshot((snapshot) => setRoomDetails(snapshot.data()));
        // }

        // db.collection("rooms")
        //     .doc(roomId)
        //     .collection("messages")
        //     .orderBy("timestamp", "asc")
        //     .onSnapshot((snapshot) =>
        //         setRoomMessages(snapshot.docs.map((doc) => doc.data()))
        //     );
        if (roomId) {
            getConvo();
            const channel = pusher.subscribe('conversation');
            channel.bind('newMessage', function (data)
            {
                getConvo();
            });

            return () =>
            {
                channel.unbind_all();
                channel.unsubscribe();
            }
        }
    }, [roomId])

    return (
        <div className="chat">
            <div className="chat__header">
                <div className="chat__headerLeft">
                    <h4 className="chat__channelName">
                        <strong> #{roomDetails} </strong>
                        {/* <strong>#general</strong> */}
                        <StarBorderOutlined />
                    </h4>
                </div>

                <div className="chat__headerRight">
                    <p><InfoOutlined /> Details</p>

                </div>
            </div>

            <div className="chat__messages">
                {roomMessages.map(({ message, timestamp, user, userImage }) => (
                    <Message
                        message={message}
                        timestamp={timestamp}
                        user={user}
                        userImage={userImage}
                    />
                ))}
            </div>
            <ChatInput channelName={roomDetails} channelId={roomId} />
        </div>
    )
}

export default Chat
